<template>
  <div>
    <loading class="loading" v-if="loading"></loading>
    <div class='cnodeTopicsWrp' ref="cnodeTopics">
      <!-- cnode -->
      <div v-show="showTabbar" class="topicTabWrp">
        <div class="tabBar">
          <button :disabled="!isTabBarActive" @click="selectTab" v-for="(tab, index) in tabs" :key="index" :id="tab.type" :class="{'tabBarActive': tab.type === currentTab}">
            {{tab.name}}
          </button>
        </div>
      </div>
      <!--  -->
      <button class="backToTopBtn" v-show="backToTopBtn" @click="backToTop"></button>
      <!-- cnode -->
      <div class='cnodeTopicsContent'>
        <div v-for='item of content' :key='item.id' class="topicItem">
          <router-link v-bind:to='{name: "UserRoute", params: {name: item.author.loginname}}'>
            <img v-bind:src='item.author.avatar_url' v-bind:title='item.author.loginname' />
          </router-link>
          <div class='articleTextInfo'>
            <router-link v-bind:to='{name:"ArticleRoute", params:{id:item.id}}'>
              <div v-if="item.top" class="topTag"></div>
              {{item.title}}
            </router-link>
            <div class='articleSubInfo'>
              <span>{{item.reply_count}}</span>
              <!-- <span>{{String(item.create_at).slice(0, 10)}}</span> -->
              <span>{{item.create_at}}</span>
            </div>
          </div>
        </div>
        <div v-show="loadingBlock" class="loadingBlock">
          <img src="../../../assets/loadingBlock.gif">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import loading from '../../common/loading.vue'
import request from '../../../util/apiRequest.js'
import commonUtil from '../../../util/common.js'

var scrollPosition = sessionStorage['scrollPosition']

window.onbeforeunload = function () {}
export default {
  components: {
    'loading': loading
  },
  name: 'MainSection',
  data: function () {
    return {
      content: [],
      tabs: [
        { type: 'all', name: '' },
        { type: 'good', name: '' },
        { type: 'share', name: '' },
        { type: 'ask', name: '' },
        { type: 'job', name: '' },
        { type: 'dev', name: '' }
      ],
      // tab all, good, share, ask, job, dev
      currentTab: 'all',
      // 
      limit: 12,
      loading: false,
      loadingBlock: false,
      backToTopBtn: false,
      showTabbar: true,
      scrollTopBefore: 0,
      // tabbar
      isTabBarActive: true
    }
  },
  methods: {
    scrollMethod: function () {
      var viewHeight = this.$refs.cnodeTopics.offsetHeight
      var scrollTop = this.$refs.cnodeTopics.scrollTop
      var scrollHeight = this.$refs.cnodeTopics.scrollHeight
      sessionStorage['scrollPosition'] = this.$refs.cnodeTopics.scrollTop
      // console.log('' + ':' + viewHeight + ', ' + ':' + scrollTop + ', ' + ':' + scrollHeight)
      // 
      if (this.scrollTopBefore < scrollTop) {
        // 
        this.showTabbar = true
      } else {
        // 
        if (scrollTop < 100) {
          // (scrollTop=0)
          this.showTabbar = true
        } else {
          this.showTabbar = false
        }
      }
      // 
      this.scrollTopBefore = scrollTop
      if ((viewHeight + scrollTop >= scrollHeight) && (viewHeight !== 0)) {
        // loadingloading
        if (!this.loading) {
          this.loadingBlock = true
        }
        this.getData(this.currentTab)
      }
      // 1000
      if (scrollTop > 500) {
        this.backToTopBtn = true
      } else {
        this.backToTopBtn = false
      }
    },
    // 
    getData: function (currentTab) {
      this.limit += 3
      // cnode
      request.getCnodeTopics({
        page: 1,
        limit: this.limit,
        tab: currentTab
      }, (res) => {
        for (let i = 0; i < res.data.data.length; i++) {
          res.data.data[i].create_at = commonUtil.transformTimeInterval(res.data.data[i].create_at)
        }
        this.content = res.data.data
        this.loading = false
        this.loadingBlock = false
        this.isTabBarActive = true
      }, (err) => {
        console.log(err.response.data.success)
      })
    },
    // 
    backToTop: function () {
      commonUtil.smoothScroll(0, this.$refs.cnodeTopics)
      // this.$refs.cnodeTopics.scrollTop = 0
    },
    // cnodetab
    selectTab: function (e) {
      if (this.currentTab !== e.target.id) {
        // limit
        // ,tab
        this.$refs.cnodeTopics.scrollTop = 0
        this.loading = true
        this.limit = 12
        this.currentTab = e.target.id
        // id
        sessionStorage['currentTab'] = this.currentTab
        this.isTabBarActive = false
        // 
        this.getData(this.currentTab)
      } else {
        console.log('')
      }
    }
  },
  // createdwatch/event
  created: function () {
    // vue
    this.loading = true
    // 
    if (!sessionStorage['currentTab']) {
      sessionStorage['currentTab'] = this.currentTab
    } else {
      this.currentTab = sessionStorage['currentTab']
    }
    this.getData(this.currentTab)
  },
  // scroll
  mounted: function () {
    window.addEventListener('scroll', this.scrollMethod, true)
  },
  beforeRouteLeave: function (to, from, next) {
    // 
    scrollPosition = this.$refs.cnodeTopics.scrollTop
    next()
  },
  beforeRouteEnter: function (to, from, next) {
    // 
    next(vm => {
      vm.$refs.cnodeTopics.scrollTop = scrollPosition
    })
  }
}
</script>

<style scoped>
a {
  text-decoration: none;
}
.loading {
  background: rgba(255, 255, 255, 0.5);
  z-index: 99!important;
}
.cnodeTopicsWrp {
  padding: 10px;
  background: #f6f6f6;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  position: absolute;
  overflow-y: auto;
}
/* tab */
@media only screen and (min-width: 900px) {
  .topicTabWrp{
    max-width: 900px;
    height: 70px;
    padding: 10px;
    margin: 0 auto;
    position: fixed;
    transition: left 0.4s ease;
    top: 60px;
    left: 250px;
    right: 0px;
    box-sizing: border-box;
    z-index: 110;
  }
  .tabBar > button:hover {
    background: #c60023;
    color: #fff;
  }
}
@media only screen and (max-width: 900px) {
  .topicTabWrp{
    max-width: 900px;
    height: 70px;
    padding: 10px 18px 10px 10px;
    margin: 0 auto;
    position: fixed;
    transition: left 0.4s ease;
    top: 60px;
    left: 0px;
    right: 0px;
    box-sizing: border-box;
    z-index: 110;
  }
}
.tabBar {
  width: 100%;
  border: 1px solid #bbb;
  margin: 0 auto;
  box-shadow: 0px 0px 10px #ccc;
  background: #fff;
  display: block;
  position: relative;
  overflow: hidden;
}
 .tabBar > button {
  width: 14.66%;
  height: 40px;
  line-height: 40px;
  border: none;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 1%;
  margin-right: 1%;
  background: #fff;
  text-align: center;
  display: block;
  float: left;
  cursor: pointer;
  user-select: none;
  outline: none;
 }
 .tabBarActive {
  background: #c60023!important;
  color: #fff;
 }

/* cnode */
.cnodeTopicsContent {
  max-width: 800px;
  height: auto;
  margin: 0 auto;
  margin-top: 60px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px #ccc;
  background: #fff;
  display: flex;
  flex-direction: column;
  font-size: 1.5rem;
}
.cnodeTopicsContent>div {
  padding: 0.5rem;
  border-bottom: 1px solid #c4c4c4;
  display: flex;
  flex-direction: row;    /* flex-directionrow */
  align-items: center;    /* flex(or) */
}
.loadingBlock {
  width: 90%;
  height: 4rem;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
}
.loadingBlock img {
  width: 50%;
  margin: 0 auto;
}
.cnodeTopicsContent .topicItem img {
  width: 4rem;
  height: 4rem;
  border-radius: 5px;
  box-shadow: 0px 0px 10px #ccc;
  margin-right: 2rem;
}
.articleTextInfo {
  display: flex;
  flex-direction: column;
  width: 100%;
}
/*   */
.articleTextInfo a {
  color: #000;
  font-size: 1rem;
  font-weight: bold;
}
/*   */
.articleTextInfo a:visited {
  color: #999;
}
.topTag {
  height: 1.0rem;
  line-height: 1.1rem;
  width: 35px;
  border-radius: 3px;
  margin-right: 5px;
  background: #c60023;
  font-size: .7rem;
  color: #fff;
  text-align: center;
  float: left;
}

/*   */
.articleSubInfo {
  font-size: 0.7rem;
  margin-top: 1rem;
  color: #666;
}
.articleSubInfo span:first-child {
  margin-right: 1.5rem;
}
/*  */
.backToTopBtn {
  width: 2rem;
  height: 6rem;
  border: 2px solid black;
  border-radius: 5px;
  background: rgba(85, 85, 85, 0.5);
  position: fixed;
  right: 2rem;
  bottom: 12rem;
  outline: none;
  color: #fff;
  font-size: 1rem;
}
.backToTopBtn:hover {
  background: #c60023;
}
</style>



