<template>
  <div class="content-wrap">
    <div v-show="displayContentMask" @click="hideMobilSidebar" class="content-mask">content</div>
    <router-view name="defaultContent"></router-view>
    <keep-alive>
      <router-view name="cnodejsTopic"></router-view>
    </keep-alive>
    <router-view name="user"></router-view>
    <router-view name="article" :loginStatus="loginStatus" v-if="isRouterAlive"></router-view>
    <!--  -->
    <div v-if="loginStatus">
      <router-view name="createTopic" v-if="isRouterAlive"></router-view>
      <router-view name="cnodeProfile"></router-view>
      <router-view name="messages" v-if="isRouterAlive"></router-view>
    </div>
    <router-view name="getMockdata"></router-view>
    <router-view name="notFoundComponent"></router-view>
    <loading v-show="loading"></loading>
  </div>
</template>

<script>
import bus from '../util/eventBus.js'
import loading from './common/loading.vue'

export default {
  components: {
    'loading': loading
  },
  data: function () {
    return {
      hideBarOrder: true,
      displayContentMask: false,
      isRouterAlive: true,
      loading: true,
      // 
      pageShouldLogin: [
        'createTopic',
        'profile',
        'messages'
      ],
      loginStatus: false
    }
  },
  methods: {
    hideMobilSidebar: function () {
      // 
      bus.$emit('hide-mobil-sidebar', this.hideBarOrder)
      // 
      bus.$emit('resetMobilsideBtn', this.hideBarOrder)
      // 
      this.displayContentMask = false
    },
    // 
    reload: function () {
      this.isRouterAlive = false
      this.$nextTick(() => {
        this.isRouterAlive = true
      })
    },
    // 
    manageView: function () {
      if (!this.loginStatus) {
        var currentPage = this.$route.path.split('/').pop()
        if (this.pageShouldLogin.indexOf(currentPage) + 1) {
          this.loading = true
          alert('')
          bus.$emit('openLoginCard', true)
        } else {
          this.loading = false
        }
      }
    }
  },
  watch: {
    $route: function () {
      this.manageView()
    }
  },
  mounted: function () {
    // headercontent
    bus.$on('displayContentMask', (msg) => {
      this.displayContentMask = msg
    })
    // 
    bus.$on('loginStatus', (loginStatus) => {
      this.loginStatus = loginStatus
      this.loading = !this.loginStatus
      this.manageView()
    })
    // urlpageShouldLoginloading
    for (var i = 0; i < this.pageShouldLogin.length; i++) {
      if (this.pageShouldLogin[i] !== this.$route.path.split('/')[2]) {
        this.loading = false
      } else {
        // ,,,loading
        this.loading = true
        break
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.content-wrap {
  top:0px;
  bottom: 0px;
  left: 260px;
  right: 0px;
  background: #fff;
  text-align: left;
  position: absolute;
  overflow-y: auto;
  overflow-x: hidden;
}

/*window.onresize*/
@media only screen and (max-width: 900px) {
  .content-wrap {
    transition: left 0.4s ease;
    left: 0px;
  }
  /* 900px */
  .content-mask {
    top: 60px;
    bottom: 50px;
    left:0px;
    right: 0px;
    opacity: 0.2;
    background: #ccc;
    position: fixed;
    z-index: 140;
  }
}
@media only screen and (min-width: 900px) {
  /* 900 */
  .content-mask {
    display: none;
  }
  .content-wrap {
    transition: left 0.4s ease;
    left: 260px;
  }
}
</style>



